/* Tags */
.tags {
    list-style: none;
    margin: 0;
    overflow: hidden; 
    padding: 0;
  }
  .tags li {
    float: left; 
  }
  .tag {
    background: #eee;
    border-radius: 3px 0 0 3px;
    color: #999;
    display: inline-block;
    height: 26px;
    line-height: 26px;
    padding: 0 20px 0 23px;
    position: relative;
    margin: 0 15px 10px 0;
    text-decoration: none;
  }
  .tag::before {
    background: #fff;
    border-radius: 10px;
    box-shadow: inset 0 1px rgba(0, 0, 0, 0.25);
    content: '';
    height: 6px;
    left: 10px;
    position: absolute;
    width: 6px;
    top: 10px;
    text-decoration: none;
  }
  .tag::after {
    background: #fff;
    border-bottom: 13px solid transparent;
    border-left: 10px solid #eee;
    border-top: 13px solid transparent;
    content: '';
    position: absolute;
    right: 0;
    top: 0;
    text-decoration: none;
  }
  .tag:hover {
    background-color: #1DA1F2;
    color: white;
    text-decoration: none;
  }
  .tag:hover::after {
     border-left-color: #1DA1F2; 
     text-decoration: none;
  }

